.aboutblackbg {
  background-color: #1d1d1d;
  margin: auto;
  height: 420px;
  padding-top: 88px;
  @media (max-width: 768px) {
    width: 100%;
    height: fit-content;
    padding: 60px 20px 70px;
    word-break: break-word;
  }
}

.aboutbtn {
  margin-top: 50px;
  width: 300px;
  height: 36px;
  line-height: 32px;
  text-align: center;
  font-weight: bold;
  border: 2px solid #ec407a;
  border-radius: 50px;
  cursor: pointer;
  @media (max-width: 768px) {
    margin: 50px auto auto;
  }
  &:hover {
    opacity: 0.8;
  }
  a,
  a:hover,
  :active,
  a:focus {
    color: white;
  }
}
.aboutwant {
  background-color: #fffbfc;
  margin: auto;
  padding-top: 120px;
  padding-bottom: 120px;
  padding-left: calc((100% - 1140px) / 2);
  padding-right: calc((100% - 1140px) / 2);
  display: flex;
  justify-content: space-between;
  @media (max-width: 768px) {
    width: 100%;
    display: block;
    padding: 0;
  }

  .text {
    width: 400px;
    margin-top: 80px;
    line-height: 50px;
    @media (max-width: 768px) {
      width: calc(100% - 40px);
      margin-left: 20px;
      margin-right: 20px;
      text-align: center;
    }
    > :nth-child(2) {
      margin-top: 30px;
    }
  }
}
.aboutPinkbg {
  width: 100%;
  height: 300px;
  background-color: #ec407a;
  @media (max-width: 768px) {
    height: 360px;
    padding: 0 10px;
  }
  a,
  a:hover,
  :active,
  a:focus {
    color: white;
  }
}
