.cloudbg {
  width: 100%;
  background: url(../../assets/imgs/imgs/cloud.png) center / contain no-repeat;
}
.point-wrap {
  width: 1000px;
  margin: 60px auto 0;
  @media (max-width: 768px) {
    width: calc(100% - 40px);
    flex-wrap: wrap;
    > div {
      margin-top: 60px;
      &:first-child {
        margin-top: 0;
      }
    }
  }
  display: flex;
  justify-content: space-evenly;
  position: relative;
}
